import React, { useCallback, useState, lazy, Suspense } from 'react';
import {
  Button,
  TextInput,
  NumberInput,
  Select,
  SelectItem,
  Checkbox,
  DataTable,
  TableRow,
  TableHead,
  TableHeader,
  TableContainer,
  TableCell,
  Table,
  TableBody,
  TableToolbar,
  TableToolbarContent,
} from 'carbon-components-react';

import Collapsible from 'react-collapsible';
import { Tabs, Tab, TabPanel, TabList } from 'react-web-tabs';
import ReactTooltip from "react-tooltip";

import { safeLoad, getDivHeight, jsonToYaml, updateStyle, updateStyle2, validateField, toggleDescription } from '../../helpers';


const AdvancedconfigurationvalueDeleteModal = lazy(() => import('./advancedconfigurationvalueDeleteModal'));
const AdvancedconfigurationvalueAdd = lazy(() => import('./advancedconfigurationvalueAdd'));
const AdvancedconfigurationvalueEdit = lazy(() => import('./advancedconfigurationvalueEdit'));


  const handleChange = () => {
  };

  const handleSave = (loadGrid, idPrefix, doClose) => {

    if(idPrefix === undefined) idPrefix = "";

    var json = {};
      
    if(document.getElementById("advanced_configuration_entry_id" + idPrefix).value.endsWith(":") !== true) {
      json["advanced_configuration_entry"] = document.getElementById("advanced_configuration_entry_id" + idPrefix).value;
    }
    if(document.getElementById("advanced_configuration_operation_id" + idPrefix).value.endsWith(":") !== true) {
      json["advanced_configuration_operation"] = document.getElementById("advanced_configuration_operation_id" + idPrefix).value;
    }
    if(document.getElementById("advanced_configuration_stanza_id" + idPrefix).value.endsWith(":") !== true) {
      json["advanced_configuration_stanza"] = document.getElementById("advanced_configuration_stanza_id" + idPrefix).value;
    }
    json["advanced_configuration_value"] = advanced_configuration_valuerowData.advanced_configuration_value;
      
    loadGrid( json["advanced_configuration_entry"], json["advanced_configuration_operation"], json["advanced_configuration_stanza"], json["advanced_configuration_value"], true); 

    handleClose(idPrefix, doClose);
  };

  const handleAdd = (setadvanced_configuration_value, idPrefix, doAdd, doClose) => {
    if(idPrefix === undefined) idPrefix = "";


    document.getElementById("advanced_configuration_entry_id").value = "";
    document.getElementById("advanced_configuration_operation_id").value = "";
    document.getElementById("advanced_configuration_stanza_id").value = "";
    advanced_configuration_valuerowData.advanced_configuration_value = []
    setadvanced_configuration_value(advanced_configuration_valuerowData.advanced_configuration_value);


    if(doAdd) {
      doAdd();
    }

    handleClose(idPrefix,  doClose);
  };

  const handleClose = (idPrefix, doClose) => {
    updateStyle("advanced_div" + idPrefix, "display", "block");
    document.getElementById("addadvanced_configurationPage" + idPrefix).style.display = "none";

      if(doClose) {
        doClose();
      }
  };

  const closeAdd = (idPrefix) => {
      if(idPrefix === undefined) idPrefix = "";

      updateStyle("advanced_configuration_div" + idPrefix, "display", "block");
      document.getElementById("advanced_configuration_tab" + idPrefix).style.display = "flex";

  };

const advanced_configuration_valueheaderData = [
    {
    header: 'Value',
    key: 'value',
  },

];

const advanced_configuration_valuerowData = {advanced_configuration_value: []};

const advanced_configuration_valuehandleAdd = (idPrefix, clearFields) => {
    if(idPrefix === undefined) idPrefix = "";

    // Set the fields to empty
    if(clearFields) {
      for(var i = 0; i < clearFields.length; i++) {
        eval(clearFields[i]);
      }
    }

    document.getElementById("advanced_configuration_value_value_id" + idPrefix).value = "";
    // Hide the tab page
    updateStyle("advanced_configuration_div" + idPrefix, "display", "none");
    // Show the add page
    document.getElementById("addadvancedconfiguration_valuePage" + idPrefix).style.display = "block";
    // Hide the remaining fields
    document.getElementById("advanced_configuration_tab" + idPrefix).style.display = "none";

  };

const Advanced_configurationAdd = (props) => {

    var row = props.row;
    const loadGrid = props.loadGrid;
    const doClose = props.doClose;
    const idPrefix = (props.idPrefix)?props.idPrefix:"";

    const [advanced_configuration_value, setadvanced_configuration_value] = React.useState([]);
    const [fetchedadvanced_configuration_value, setFetchedadvanced_configuration_value] = React.useState(false);

    React.useEffect(() => {
      advanced_configuration_valueloadGrid();
    }, [fetchedadvanced_configuration_value]);

    const advanced_configuration_valueloadGrid = React.useCallback(async (advanced_configuration_value_value, add) => {
      try {
        var attrs = [];
        if(advanced_configuration_value_value) {
          if(add) {
            for(var i = 0; i < advanced_configuration_valuerowData.advanced_configuration_value.length; i++) {
              var tempId = ((advanced_configuration_valuerowData.advanced_configuration_value[i].value)?advanced_configuration_valuerowData.advanced_configuration_value[i].value:"") + "";
              if((window.advanced_configuration_valueediting != true && window.advanced_configuration_valueediting != "true") || tempId != window.advanced_configuration_valuerowId) {
                attrs.push(advanced_configuration_valuerowData.advanced_configuration_value[i]);
              }
            }
            var newId = ((advanced_configuration_value_value)?advanced_configuration_value_value:"") + "";
            attrs.push({
              id: newId,
              value: advanced_configuration_value_value,
            });
          } else {
            // Must be a delete
            for(var i = 0; i < advanced_configuration_valuerowData.advanced_configuration_value.length; i++) {
              if(advanced_configuration_valuerowData.advanced_configuration_value[i].value !== advanced_configuration_value_value ) {
                attrs.push(advanced_configuration_valuerowData.advanced_configuration_value[i]);
              }
            }
          }
        } else {
          attrs = advanced_configuration_valuerowData.advanced_configuration_value;
        }
        advanced_configuration_valuerowData.advanced_configuration_value = attrs;
        const data = {advanced_configuration_value: attrs};
        setadvanced_configuration_value(data.advanced_configuration_value);

        if(advanced_configuration_value_value !== undefined && advanced_configuration_value_value !== null ) {
            handleChange();

        }

      } catch (e) {
        console.log(e);
      }
      return true;
    });



    const [advanced_configuration_entry, setadvanced_configuration_entry] = useState(''),
    updateadvanced_configuration_entry = useCallback((evt) => setadvanced_configuration_entry(evt.target.value), []);

    const [advanced_configuration_operation, setadvanced_configuration_operation] = useState(''),
    updateadvanced_configuration_operation = useCallback((evt) => setadvanced_configuration_operation(evt.target.value), []);

    const [advanced_configuration_stanza, setadvanced_configuration_stanza] = useState(''),
    updateadvanced_configuration_stanza = useCallback((evt) => setadvanced_configuration_stanza(evt.target.value), []);

    const [advanced_configuration_value_value, setadvanced_configuration_value_value] = useState(''),
    updateadvanced_configuration_value_value = useCallback((evt) => {
      setadvanced_configuration_value_value(evt.target.value);
      var tempId = evt.target.value;
      validateField(advanced_configuration_valuerowData.advanced_configuration_value, setadvanced_configuration_value_value_valid, setadvanced_configuration_value_value_invalid_text, "Advanced Configuration Value", "value", tempId);
    }, []);

    const [advanced_configuration_value_value_valid, setadvanced_configuration_value_value_valid] = useState(true);
    const [advanced_configuration_value_value_invalid_text, setadvanced_configuration_value_value_invalid_text] = useState("");



    const [fetcheddata, setFetcheddata] = React.useState(false);
    React.useEffect(() => {
      dataLoad();
    }, [fetcheddata]);
    const dataLoad = () => {

      for(var i = 0; i < advanced_configuration_valuerowData.advanced_configuration_value.length; i++) {
        if(advanced_configuration_valuerowData.advanced_configuration_value[i].id === window.advanced_configuration_valuerowId) {
        setadvanced_configuration_value_value((advanced_configuration_valuerowData.advanced_configuration_value[i] !== undefined && advanced_configuration_valuerowData.advanced_configuration_value[i].value !== undefined )?advanced_configuration_valuerowData.advanced_configuration_value[i].value:"");
        }
      }


      return true;
    };

    const setDataMain = (functionName, data) => {
      if(functionName && data) {
        eval(functionName)(data);
      }
    };

    return (
      <div>
        
        <div id={'addadvanced_configurationPage' + idPrefix} style={{display:'none'}} className="bx--col bx--no-gutter">

          <Tabs id= {"advanced_configuration_tab" + idPrefix} defaultTab={"General_tab" + idPrefix} vertical className="vertical-tabs" style={{overflow:'auto'}}>
              <TabList>
                <Tab tabFor={"General_tab" + idPrefix}>General</Tab>
                <Tab tabFor={"value_tab" + idPrefix}>Value</Tab>
              </TabList>
              <TabPanel style={{width:'100%', overflow:'auto'}} tabId={"value_tab" + idPrefix}>
                <div className="iag_sublevel_tabs" style={{height:'60vh', overflow:'auto'}}>
                <div className= {"iag_form_elem iag_tab_heading advanced_configuration_div" + idPrefix}>
                  <span className="iag_breadcrumb">Advanced / Configuration : </span><br/>                  <h2>Value Settings</h2><hr/>
                </div>
                <div className={"iag--simple-div advanced_configuration_div" + idPrefix}>
                <div className= {"iag_form_elem advanced_configuration_div" + idPrefix}>
  <h3 tabIndex="0"  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"advanced_configuration_value_tooltip_id" + idPrefix} >Value</h3><hr/>
</div>
          <ReactTooltip id={"advanced_configuration_value_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The value(s) for the new configuration entry. The value is only required for the `add` and `set` operations.<br/></div></ReactTooltip>

                <div id={"advanced_configuration_valueTableDiv" + idPrefix} style={{marginLeft:"10px",marginBottom:"10px"}}>
                  <div className={"iag_form_elem advanced_configuration_div" + idPrefix}>
                    <DataTable id={"advanced_configuration_valueTable" + idPrefix} isSortable
                        headers={advanced_configuration_valueheaderData}
                        rows={advanced_configuration_valuerowData.advanced_configuration_value}
                        render={({
                          rows,
                          headers,
                          getHeaderProps,
                          getRowProps,
                          getTableProps,
                          getToolbarProps,
                          onInputChange,
                          getTableContainerProps
                    }) => (
                      <TableContainer>
                        <TableToolbar aria-label="data table toolbar">
                          <TableToolbarContent>
                            <Button onClick={() => {advanced_configuration_valuehandleAdd(idPrefix, [setadvanced_configuration_value_value(''),    setadvanced_configuration_value_value_valid(true),]);}}>Add</Button>
                          </TableToolbarContent>
                        </TableToolbar>
                        <Table size='normal' {...getTableProps()}>
                          <TableHead>
                            <TableRow>
                              {headers.map(header => (
                                <TableHeader {...getHeaderProps({ header })}>
                                  {header.header}
                                </TableHeader>
                              ))}
                              <TableHeader>
                                  Actions
                              </TableHeader>
                            </TableRow>
                          </TableHead>
                          <TableBody>
                            {rows.map(row => (
                              <TableRow key={row.id}>
                                {row.cells.map(cell => (
                                  <TableCell key={cell.id}>{cell.value}</TableCell>
                                ))}
                                <TableCell>
                                  <Suspense fallback={<div></div>}>
                                    <AdvancedconfigurationvalueDeleteModal row={row} loadGrid={advanced_configuration_valueloadGrid} />
                                  </Suspense>
                                  <Suspense fallback={<div></div>}>
                                    <AdvancedconfigurationvalueEdit  data={advanced_configuration_valuerowData.advanced_configuration_value} setData={setDataMain} row={row} loadGrid={advanced_configuration_valueloadGrid} dataLoad={dataLoad} editing={true} name={"advanced_configuration_value"} idPrefix={idPrefix + "_add"} parentIdPrefix={idPrefix} 
            updateadvanced_configuration_value_value={updateadvanced_configuration_value_value} advanced_configuration_value_value={advanced_configuration_value_value}
            advanced_configuration_value_value_valid={advanced_configuration_value_value_valid}
            advanced_configuration_value_value_invalid_text={advanced_configuration_value_value_invalid_text}
                                    />
                                  </Suspense>
                                </TableCell>
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </TableContainer>)}
                    />
                  </div>
                </div>

                </div>
                </div>
                  <div className= {"iag_form_elem advanced_configuration_div" + idPrefix}>
                    <Button onClick={() => { handleClose(idPrefix,  doClose); }}>Cancel</Button>
                    <Button disabled={(!props.advanced_configuration_entry || !props.advanced_configuration_entry_valid) || (!props.advanced_configuration_stanza || !props.advanced_configuration_stanza_valid) || (!props.advanced_configuration_operation || !props.advanced_configuration_operation_valid)} onClick={() => { handleSave(loadGrid, idPrefix,  doClose); }}>Save</Button>
                  </div>
              </TabPanel>
              <TabPanel style={{width:'100%', overflow:'auto'}} tabId={"General_tab" + idPrefix}>
                <div className="iag_sublevel_tabs" style={{height:'60vh', overflow:'auto'}}>
                <div className= {"iag_form_elem iag_tab_heading advanced_configuration_div" + idPrefix}>
                  <span className="iag_breadcrumb">Advanced / Configuration : </span><br/>                  <h2>General Settings</h2><hr/>
                  Specifies the general settings associated with this component
                </div>
                <div className={"iag--simple-div advanced_configuration_div" + idPrefix}>
                <div className= {"iag_form_elem advanced_configuration_div" + idPrefix}>
          <TextInput  invalid={!props.advanced_configuration_entry_valid}  invalidText={props.advanced_configuration_entry_invalid_text}  value={props.advanced_configuration_entry}  id={"advanced_configuration_entry_id" + idPrefix}  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"advanced_configuration_entry_tooltip_id" + idPrefix}  labelText="Entry *" onChange={props.updateadvanced_configuration_entry}/>
          <ReactTooltip id={"advanced_configuration_entry_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The name of the configuration entry.<br/></div></ReactTooltip>
        </div>

                <div className= {"iag_form_elem advanced_configuration_div" + idPrefix}>
                <Select
                  value={props.advanced_configuration_operation}
                   id={"advanced_configuration_operation_id" + idPrefix}
 onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"advanced_configuration_operation_tooltip_id" + idPrefix}                   labelText="Operation *"
                  onChange={props.updateadvanced_configuration_operation}>
                  <SelectItem text="" value="" />
                  <SelectItem text="delete" value="delete" />
                  <SelectItem text="add" value="add" />
                  <SelectItem text="set" value="set" />
                </Select>
          <ReactTooltip id={"advanced_configuration_operation_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The operation to be perform on the configuration entry.<br/></div></ReactTooltip>
                </div>

                <div className= {"iag_form_elem advanced_configuration_div" + idPrefix}>
          <TextInput  invalid={!props.advanced_configuration_stanza_valid}  invalidText={props.advanced_configuration_stanza_invalid_text}  value={props.advanced_configuration_stanza}  id={"advanced_configuration_stanza_id" + idPrefix}  onFocus={(e) => ReactTooltip.show(e.target)} onBlur={(e) => ReactTooltip.hide(e.target)} data-tip data-for={"advanced_configuration_stanza_tooltip_id" + idPrefix}  labelText="Stanza *" onChange={props.updateadvanced_configuration_stanza}/>
          <ReactTooltip id={"advanced_configuration_stanza_tooltip_id" + idPrefix} place="top" effect="solid" ><div style={{ maxWidth: 300 }}>The name of the configuration stanza which contains the configuration entry.<br/></div></ReactTooltip>
        </div>

                </div>
                </div>
                  <div className= {"iag_form_elem advanced_configuration_div" + idPrefix}>
                    <Button onClick={() => { handleClose(idPrefix,  doClose); }}>Cancel</Button>
                    <Button disabled={(!props.advanced_configuration_entry || !props.advanced_configuration_entry_valid) || (!props.advanced_configuration_stanza || !props.advanced_configuration_stanza_valid) || (!props.advanced_configuration_operation || !props.advanced_configuration_operation_valid)} onClick={() => { handleSave(loadGrid, idPrefix,  doClose); }}>Save</Button>
                  </div>
              </TabPanel>
          </Tabs>
                  <Suspense fallback={<div></div>}>
                    <AdvancedconfigurationvalueAdd loadGrid={advanced_configuration_valueloadGrid} doClose={() => {closeAdd(idPrefix);}} idPrefix={idPrefix} 
            updateadvanced_configuration_value_value={updateadvanced_configuration_value_value} advanced_configuration_value_value={advanced_configuration_value_value}
            advanced_configuration_value_value_valid={advanced_configuration_value_value_valid}
            advanced_configuration_value_value_invalid_text={advanced_configuration_value_value_invalid_text}
                    />
                  </Suspense>
                  <Suspense fallback={<div></div>}>
                    <AdvancedconfigurationvalueEdit  loadGrid={advanced_configuration_valueloadGrid} dataLoad={dataLoad} name={"advanced_configuration_value"} idPrefix={idPrefix + "_add"} parentIdPrefix={idPrefix} 
            updateadvanced_configuration_value_value={updateadvanced_configuration_value_value} advanced_configuration_value_value={advanced_configuration_value_value}
            advanced_configuration_value_value_valid={advanced_configuration_value_value_valid}
            advanced_configuration_value_value_invalid_text={advanced_configuration_value_value_invalid_text}
                    />
                  </Suspense>
        </div>
        </div>
    );
}

export default Advanced_configurationAdd;
